import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import UiUxDescription from "../../../components/ui-ux-design-components/UiUxDescription/UiUxDescription";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import UiUxLine from "../../../components/ui-ux-design-components/UiUxLine/UiUxLine";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"
import pic4 from "./pic4.png"
import pic5 from "./pic5.png"
import pic6 from "./pic6.png"
import pic7 from "./pic7.png"
import pic8 from "./pic8.png"
import pic9 from "./pic9.png"
import pic10 from "./pic10.png"

import banner from "./UIUX.jpg"

import { useLocation } from "react-router-dom"

export default function UiUxDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="ui-ux-design-page">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="UI/UX Design"
        bannerParagraph="Crafting Seamless Digital Experiences: UI/UX Design Services"
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={banner}
      />

      <UiUxDescription />

      <SubCategoriesCardLeft
        heading="Research and Discovery"
        paragrapgh="The process begins with thorough research to understand the target audience, industry trends, and competitors. User interviews, surveys, and market analysis help gather valuable insights."
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        heading="User Personas and User Journey Mapping"
        paragraph="User personas are created to represent different user types, their goals, and their behavior. User journey mapping visualizes the user's interactions and touchpoints with the product or service."
        button="LET'S CHAT"
        picture={pic2}
      />

      <SubCategoriesCardLeft
        heading="Information Architecture"
        paragrapgh="This step involves organizing and structuring content to ensure logical and intuitive navigation. Creating site maps and user flow diagrams helps define the overall structure of the interface."
        button="LET'S CHAT"
        picture={pic3}
      />

      <SubCategoriesCardRight
        heading="Wireframing"
        paragraph="Wireframes are basic, low-fidelity representations of the interface. They outline the placement of key elements, functionality, and content hierarchy without getting into visual details. Wireframes serve as a blueprint for the design."
        button="LET'S CHAT"
        picture={pic4}
      />

      <SubCategoriesCardLeft
        heading="Visual Design"
        paragrapgh="Once the wireframes are approved, the visual design phase begins. This includes creating high-fidelity mockups that incorporate branding elements, typography, color schemes, and visual aesthetics. Design tools like Sketch, Adobe XD, or Figma are often used."
        button="LET'S CHAT"
        picture={pic5}
      />

      <SubCategoriesCardRight
        heading="Prototyping"
        paragraph="Interactive prototypes are developed to simulate the user experience and test usability. Prototypes allow stakeholders and users to provide feedback and validate design decisions before development begins. Tools like Invison or Marvel facilitate prototyping."
        button="LET'S CHAT"
        picture={pic6}
      />

      <SubCategoriesCardLeft
        heading="Usability Testing"
        paragrapgh="Usability testing involves observing and gathering feedback from users interacting with the prototype. It helps identify any pain points, usability issues, or areas for improvement. The findings are used to refine the design."
        button="LET'S CHAT"
        picture={pic7}
      />

      <SubCategoriesCardRight
        heading="Handoff and Collaboration"
        paragraph="The final design is prepared for handoff to the development team. Collaboration between designers and developers ensures a smooth transition and clear communication of design specifications."
        button="LET'S CHAT"
        picture={pic8}
      />

      <SubCategoriesCardLeft
        heading="Implementation and Quality Assurance"
        paragrapgh="The development team brings the design to life, translating it into a functional user interface. Quality assurance is conducted to ensure that the design is accurately implemented and functions as intended."
        button="LET'S CHAT"
        picture={pic9}
      />

      <SubCategoriesCardRight
        heading="Post-Launch Evaluation"
        paragraph="Once the product is launched, user feedback and analytics are gathered to evaluate the effectiveness of the design. This data helps identify areas of improvement and informs future design iterations."
        button="LET'S CHAT"
        picture={pic10}
      />

      <UiUxLine/>

      <br/>
      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>

      <Footer/>
    </div>
  );
}
